*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --brend-color: #f87719;
  --brend-color-active: #188ce8;
  --main-background: #111;
  --btn-background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --star-background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --card-background: linear-gradient(180deg, rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  ---hero-background: linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%), #0e0e0e;
  --main-text-color: #fff;
  --second-text-color: #f8f8f8;
  --grey-text-color: #111;
  --main-gray-color: #595959;
  --main-white-color: #fff;
  --main-black-color: #000 --hero-background-color: #2f303a;
  --btn-border-radius: 74px;
  --icons-border-radius: 50%;
  --cards-border-radius: 5px;
  --modal-border-radius: 20px;
  --cards-box-shadow: 1px 1px 24px 2px rgba(1, 1, 1, .45);
  --modal-box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
  --cubic: cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--main-background);
  font-family: Roboto, sans-serif;
  transition: background-color .4s ease-out;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1280px;
  }
}

.section-catalog {
  margin-top: 80px;
}

.catalog {
  grid-template-columns: 1fr;
  display: grid;
}

@media screen and (min-width: 768px) {
  .catalog {
    grid-gap: 16px;
    grid-template-columns: 1fr 1fr 1fr;
    display: grid;
  }
}

@media screen and (min-width: 1280px) {
  .catalog {
    grid-gap: 16px;
    grid-template-columns: 1fr 1fr 1fr;
    display: grid;
  }
}

.catalog__card {
  color: #f0f8ff;
  width: 280px;
  height: 406px;
  cursor: pointer;
  transition: scale .3s var(--cubic);
  display: inline-block;
  position: relative;
  scale: 1;
}

@media screen and (min-width: 768px) {
  .catalog__card {
    width: 224px;
    height: 325px;
  }
}

@media screen and (min-width: 1280px) {
  .catalog__card {
    width: 395px;
    height: 574px;
  }
}

.catalog__card:hover {
  scale: 1.03;
}

.catalog__img-wrapper {
  width: 100%;
  height: 100%;
  display: inline-block;
  position: relative;
}

.catalog__img-wrapper:after {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(255, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  position: absolute;
  top: 0;
  left: 0;
}

.catalog__img {
  width: 100%;
  height: 100%;
}

.catalog__info {
  width: 256px;
  position: absolute;
  bottom: 12px;
  left: 12px;
}

@media screen and (min-width: 768px) {
  .catalog__info {
    width: 200px;
  }
}

@media screen and (min-width: 1280px) {
  .catalog__info {
    width: 355px;
    left: 20px;
  }
}

.info__wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.info__list {
  display: flex;
}

.info__title {
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 14px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
}

@media screen and (min-width: 768px) {
  .info__title {
    font-size: 12px;
    line-height: 1.5;
  }
}

@media screen and (min-width: 1280px) {
  .info__title {
    font-size: 20px;
    line-height: 1.17;
  }
}

.info__descr {
  color: #b7b7b7;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
  position: relative;
}

@media screen and (min-width: 768px) {
  .info__descr {
    line-height: 1.5;
  }
}

@media screen and (min-width: 1280px) {
  .info__descr {
    font-size: 20px;
    line-height: 1.17;
  }
}

.info__descr:not(:last-child) {
  margin-right: 14px;
}

.info__descr:first-child:after {
  content: "";
  height: 100%;
  width: 1px;
  background-color: #fff;
  display: inline-block;
  position: absolute;
  right: -7px;
}

.catalog__stars-wrap {
  width: 80px;
  height: 14px;
  margin-right: 0;
  font-size: 12px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .catalog__stars-wrap {
    width: 66px;
    height: 10px;
    font-size: 10px;
  }
}

@media screen and (min-width: 1280px) {
  .catalog__stars-wrap {
    width: 106px;
    height: 18px;
    font-size: 16px;
  }
}

.catalog__stars-wrap:before {
  content: " ★ ★ ★ ★ ★";
  display: block;
}

.catalog__rating-active {
  width: 0%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.catalog__rating-active:before {
  content: " ★ ★ ★ ★ ★";
  width: 100%;
  height: 100%;
  color: var(--brend-color);
  position: absolute;
  top: 0;
  left: 0;
}

.catalog__filter-wrapper {
  justify-content: center;
  align-items: center;
  margin-bottom: 36px;
  display: flex;
}

.catalog__input {
  background-color: var(--main-background);
  color: var(--main-white-color);
  width: 224px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 8px;
  margin-right: 16px;
  padding-top: 14.5px;
  padding-bottom: 14.5px;
  padding-left: 24px;
}

@media screen and (min-width: 768px) {
  .catalog__input {
    width: 204px;
  }
}

@media screen and (min-width: 1280px) {
  .catalog__input {
    width: 334px;
  }
}

.catalog__btn {
  width: 40px;
  height: 40px;
  background: var(--btn-background);
  color: var(--main-text-color);
  transition: color 350ms var(--cubic);
  border: none;
  border-radius: 50%;
  position: relative;
}

@media screen and (min-width: 768px) {
