}

.catalog__btn-svg {
  fill: currentColor;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.catalog__btn:hover {
  color: var(--main-background);
}

